import React, { useState } from "react";
import "../style/pengajuan.css";
import { useNavigate, useParams } from "react-router";
import Swal from "sweetalert2";
import NavbarTeam from "../component/NavbarTeam";
import Topbar from "../component/Topbar";
import axios from "axios";

const UpdateDataTolak = () => {
  const navigate = useNavigate();
  const [validationError, setValidationError] = useState({});
  const {nik_alm, id} = useParams();
  const [keterrangan, setKeterrangan] = useState("");
  
  const createProduct = async (e) => {
    e.preventDefault();

    const formData = new FormData()
    formData.append("keterrangan", keterrangan);
    formData.append("nik_alm", nik_alm);

    await axios.post(`http://localhost:8000/api/datatolak`, formData).then(({data})=>{
      Swal.fire({
        icon:"success",
        text:data.message
      })
      navigate("/kematian")
    }).catch(({response})=>{
      if(response.status===422){
        setValidationError(response.data.errors)
      }else{
        Swal.fire({
          text:response.data.message,
          icon:"error"
        })
      }
    })
  }

  return (
    <div className="app-containter">
      <Topbar />
      <NavbarTeam />
      <main className="body">
        <span className="penjelasan">
          Silahkan isi form berikut untuk mengupdate data bahwa data ditolak dan
          telah di proses selesai.
        </span>
        <form className="form-pengajuan" onSubmit={createProduct}>
          <div className="form_pertama">
            <div className="form-group">
              <label htmlFor="nama">Penjelasan Penolakan</label>
              <textarea
                className="input"
                placeholder="Alasan penolakan pengajuan"
                type="text"
                value={keterrangan}
                onChange={(event) => {
                  setKeterrangan(event.target.value);
                }}
                required
              />
            </div>
          </div>
          <button className="button" type="submit">
            Tolak
          </button>
        </form>
      </main>

      <div className="footer fixed-bottom">
        <div className="container">
          <div align="center">
            <small>Sistem Penyaluran Dana Santunan Kematian &copy; 2022</small>
          </div>
        </div>
      </div>
    </div>
  );
};

export default UpdateDataTolak;
