import React, { useEffect } from "react";
import Navbar from "./Navbar";
import Topbar from "./Topbar";
import "../style/home.css";
import Widget from "./Widget";
import "../style/beranda.css";
import { Link } from "react-router-dom";
import AddIcon from "@mui/icons-material/Add";
import axios from "axios";
import edit from "../img/edit.png";
import remove from "../img/remove.png";
import Swal from "sweetalert2";
import { useState } from "react";
import { useNavigate } from "react-router";
import { data } from "browserslist";

const Home = () => {
  const [products, setProducts] = useState([]);
  const navigate = useNavigate();
  const [items, setItems] = useState([]);
  const [isLoaded, setIsLoaded] = useState(false);
  const [error, setError] = useState(null);

  const [user, setUser] = useState({});

  const [isAuthenticated, setIsAuthenticated] = useState(false);
  // const token = localStorage.getItem("token");
  // const role = localStorage.getItem("role");
  useEffect(() => {
    const token = localStorage.getItem('token');
    const role = localStorage.getItem('role');
    if (!token || role !== 'admin') {
      navigate('/login');
      return;
    }

    setIsAuthenticated(true);
  }, []);

  useEffect(() => {
    fetch("http://localhost:8000/api/user")
      .then((res) => res.json())
      .then(
        (result) => {
          setItems(result);
          setIsLoaded(true);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );
  }, []);

  const deleteProduct = async (id) => {
    const isConfirm = await Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      return result.isConfirmed;
    });

    if (!isConfirm) {
      return;
    }

    const fetchProducts = async () => {
      await axios.get(`http://localhost:8000/api/user`).then(({ data }) => {
        setProducts(data);
      });
    };

    await axios
      .delete(`http://localhost:8000/api/user/${id}`)
      .then(({ data }) => {
        Swal.fire({
          icon: "success",
          text: data.message,
        });
        fetchProducts();
        navigate("/dashboard");
      })
      .catch(({ response: { data } }) => {
        Swal.fire({
          text: data.message,
          icon: "error",
        });
      });
  };
  if(!isLoaded){
    return (
    <div className="app-container">
      <Topbar/>
      <Navbar/>
      <div className="loading"></div>;
    </div>
    );
  }
  else{
  return (
    <div className="app-container">
      <Topbar />
      <Navbar />
      <main className="body-dashboard-satu">
        <div className="body-widget">
          <Widget type="jumlah_kematian" />
          <Widget type="anggaran_kematian" />
        </div>
        <div className="tabel-pengguna">
          <div>
            <Link className="link-penambahan" to="/dashboard/tambahaccount">
              <AddIcon />
              <span className="tambah-akun">Tambah Account</span>
            </Link>
          </div>
          <div>
            <table className="list-user">
              <thead>
                <tr className="toh">
                  <th className="lis">#</th>
                  <th className="lis">Email</th>
                  <th className="lis">Role</th>
                  <th className="lis">Tindakan</th>
                </tr>
              </thead>
              <tbody>
                {items.length &&
                  items.map((row, key) => (
                    <tr key={key} className="toh">
                      <td className="loh">{key + 1}</td>
                      <td className="loh">{row.email}</td>
                      <td className="loh">{row.role}</td>
                      <td className="loh">
                        <div className="action">
                          <div className="editdong">
                            <div
                              onClick={() => deleteProduct(row.id)}
                              className="hapus"
                            >
                              <img src={remove} width="25px" alt="remove" className="silang"/>
                              <span id="hapus">Hapus</span>
                            </div>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
          </div>
        </div>
      </main>
      <footer className="footer">
        Sistem Penyaluran Dana Santunan Kematian &copy; 2022
      </footer>
    </div>
  );
};
}

export default Home;
